.bounding-box{
    position: absolute;
    box-shadow: 0 0 0 3px #149df2 inset;
    
    display: flex;
 
    flex-wrap: wrap;
    -ms-flex-pack: center;
    justify-content: center;
    cursor: pointer;
}
.container{
    z-index:-1;
    display: flex;
    justify-content: center;
  }
  @media screen and (max-width:700px){
    .container{
        z-index:-1;
        display: flex;
        justify-content: center;
        width: 90%;
        height: auto;
      }
  }